body {
  .block {
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;

    &__inner {
      padding: 15px 15px;
      margin: 0 auto;
      max-width: var(--max-width-desc);
      width: 100%;
    }
  }

  h1 {
    color: var(--black-100);
    text-align: center;
    font-weight: 600;
    font-size: 32px;
    line-height: 120%;
  }

  h2 {
    font-weight: 600;
    font-size: 24px;
    line-height: 130%;
  }

  h3 {
    font-weight: 600;
    font-size: 20px;
    line-height: 130%;
  }

  .type {
    color: var(--link-color);
    font-size: 12px;
    line-height: 150%;
  }

  .quote {
    border-left: 5px solid var(--link-color);
    padding-left: 30px;
  }

  .finish_line {
    width: 61px;
    border-top: 5px solid var(--link-color);
    margin-bottom: 20px;
  }

  .tooltip {
    position: relative;
    display: inline-block;

  }

  .tooltip .tooltiptext {
    visibility: hidden;
    width: auto;
    max-width: 180px;
    background-color: var(--black-80);
    color: var(--white-100);
    font-size: 12px;
    line-height: 150%;
    border-radius: 10px;
    padding: 10px 15px;
    top: 100%;
    left: 50%;
    margin-left: -60px;
    position: absolute;
    z-index: 1;
  }

  .tooltip:hover .tooltiptext {
    visibility: visible;
  }
}

@media (min-width: 769px) {
  .type {
    font-size: 14px;
  }
}

@media (min-width: 1024px) {
  body {
    h2 {
      font-weight: 600;
      font-size: 32px;
      line-height: 120%;
    }

    h3 {
      font-size: 40px;
      line-height: 110%;
    }
  }
}